.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
    background-color: rgba(0,0,0,0.75);
}

.modal {
    position: fixed;
    top: 5vh;
    left: 25%;
    width: 90%;
    background-color: #FFFFFF;
    padding: 1rem;
    border-radius: 15px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
}

@media (min-width: 768px) {
    .modal { 
        width: 40rem;
        left: calc(50%-20rem);
    }
}

@keyframes slide-down {
    from {
        opacity: 0;
        transform: translatedY(-3rem);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}